import { Component, OnInit } from '@angular/core';
import {IProduct} from "../../models/IProduct";
import {Store} from "@ngrx/store";
import {State} from "../../../reducers";
import * as productActions from '../../actions/product.actions';
import {AuthService} from "../../../root/services/auth.service";

@Component({
  selector: 'app-upload',
  templateUrl: './upload.component.html',
  styleUrls: ['./upload.component.css']
})
export class UploadComponent implements OnInit {

  public emptyForm:boolean = false;
  public product:IProduct = {
    name : '',
    brand : '',
    price : null,
    qty : null,
    image : '',
    description : '',
    usage : '',
    category : ''
  };
  public imageFileName:any;
  public result:HTMLInputElement;

  constructor(private store:Store<State>,
              public authService:AuthService) { }

  ngOnInit(): void {
  }

  public async onChangeImage(event){
    if (event.target.files && event.target.files.length) {
      const [file] = event.target.files;
      let reader = new FileReader();
      reader.readAsDataURL(file);
      this.imageFileName = file;
      reader.addEventListener('load', () => {
        return reader.result ? this.product.image = String(reader.result) : '';
      });
    }
  }

  // submitUpload
  public submitUpload(){
    if(this.product.name !== '' && this.product.brand !== '' && this.product.price !== null && this.product.qty !== null && this.product.image !== '' && this.product.category !== '' && this.product.description !== '' && this.product.usage !== ''){
      this.store.dispatch(productActions.uploadProducts({product : this.product}));
    }
    else{
      this.emptyForm = true;
    }
  }

  // closeAlert
  public closeAlert(){
    this.emptyForm = false;
  }
}
