import { Component, OnInit } from '@angular/core';
import {select, Store} from "@ngrx/store";
import {State} from "../../../reducers";
import {IProduct} from "../../models/IProduct";
import * as productActions from '../../actions/product.actions';
import * as prodReducer from '../../reducers/product.reducer';
import * as orderActions from '../../../orders/actions/order.actions';

@Component({
  selector: 'app-mens-wear',
  templateUrl: './mens-wear.component.html',
  styleUrls: ['./mens-wear.component.css']
})
export class MensWearComponent implements OnInit {

  public products:IProduct[];
  public loading:boolean;
  public errorMessage : string;
  constructor(private store:Store<State>) { }

  ngOnInit(): void {
    this.store.dispatch(productActions.getMensCollection());
    this.store.pipe(select(prodReducer.productFeatureKey)).subscribe((state) => {
      this.products = state.products;
      this.loading = state.loading;
      this.errorMessage = state.errorMessage;
    });
  }

  // clickAddToCart
  public clickAddToCart(product){
      this.store.dispatch(orderActions.addToCart({product : product, qty : 1}));
  }

}
