import { Component, OnInit } from '@angular/core';
import {ActivatedRoute, ParamMap} from "@angular/router";
import {select, Store} from "@ngrx/store";
import {State} from "../../../reducers";
import {IProduct} from "../../models/IProduct";
import * as productActions from '../../actions/product.actions';
import * as productReducer from '../../reducers/product.reducer';
import * as orderActions from '../../../orders/actions/order.actions';

@Component({
  selector: 'app-product-details',
  templateUrl: './product-details.component.html',
  styleUrls: ['./product-details.component.css']
})
export class ProductDetailsComponent implements OnInit {

  public productQty:string = '';
  public productId:string;
  public selectedProduct:IProduct;
  public loading:boolean;
  public errorMessage : string;
  constructor(private activatedRoute:ActivatedRoute,
              private store:Store<State>) { }

  ngOnInit(): void {
    this.activatedRoute.paramMap.subscribe((parms:ParamMap) => {
      this.productId = parms.get('id');
    });

    this.store.dispatch(productActions.getProductDetails({productId : this.productId}));
    this.store.pipe(select(productReducer.productFeatureKey)).subscribe((state) => {
      this.selectedProduct = state.selectedProduct;
      this.loading = state.loading;
      this.errorMessage = state.errorMessage;
    });

  }

  public clickAddToCart(){
    let selectedQty:number = (this.productQty === '') ? 1 : Number(this.productQty);
    this.store.dispatch(orderActions.addToCart({product : this.selectedProduct , qty : selectedQty}));
  }

}
